import React, { useState } from "react";
import Empezar from "./Crate-tienda/Empezar";
import Header from "./Header";

export default function Create_Tienda() {
  const Uptadate = (event) => {
    setView(event);
  };
  const [view, setView] = useState(<Empezar Uptadate={Uptadate} />);

  return (
    <section className="Create_Tienda">
      <Header  />
      {view}
    </section>
  );
}
