import React from "react";

import shop from "../../../assets/shop.png";
import Cupones from "./Cupones/Cupones";
import Productos from "./Productos/Productos";
import Resumen from "./Resumen/Resumen";

export default function Drawer({ Update, UpdateState }) {
  let items = {
    Resumen: "Resumen",
    Productos: "Productos",
    Cupones: "Cupones",
    Ventas: "Ventas",
    Envíos: "Envíos",
    Reservas: "Reservas",
    Digitales: "Digitales",
    Políticas: "Políticas",
    Turismo: "Turismo",
    Configuración: "Configuración",
  };

  const hover = (event) => {
    let marvel = items[event];

    for (var key in items) {
      let recorrido = key;

      if (marvel === recorrido) {
        document.querySelector("#" + event).classList.add("on");
      } else {
        document.getElementById(recorrido).classList.remove("on");
      }
    }
  };

  return (
    <div className="Drawer">
      <h3 style={{ gridColumn: "2", gridRow: "1" }}>Mi Nido</h3>
      <br />
      <img src={shop} alt=" " />
      <h5>Ventas</h5>
      <span
        onClick={() => {
          hover("Resumen");
          Update("Resumen", <Resumen />);
          UpdateState();
        }}
        id="Resumen"
      >
        Resumen
      </span>
      <span
        onClick={() => {
          hover("Productos");
          Update("Productos", <Productos />);
          UpdateState();
        }}
        id="Productos"
      >
        Productos
      </span>
      <span
        onClick={() => {
          hover("Cupones");
          Update("Cupones", <Cupones />);
          UpdateState();
        }}
        id="Cupones"
      >
        Cupones
      </span>
      <span
        onClick={() => {
          hover("Ventas");
          Update("Ventas");
          UpdateState();
        }}
        id="Ventas"
      >
        Ventas
      </span>
      <span
        onClick={() => {
          hover("Envíos");
          Update("Envíos");
          UpdateState();
        }}
        id="Envíos"
      >
        Envíos
      </span>
      <span
        onClick={() => {
          hover("Reservas");
          Update("Reservas");
          UpdateState();
        }}
        id="Reservas"
      >
        Reservas
      </span>
      <span
        onClick={() => {
          hover("Digitales");
          Update("Digitales");
          UpdateState();
        }}
        id="Digitales"
      >
        Reservas Digitales
      </span>
      <h5>Políticas</h5>
      <span
        onClick={() => {
          hover("Políticas");
          Update("Políticas");
          UpdateState();
        }}
        id="Políticas"
      >
        Políticas tienda
      </span>
      <span
        onClick={() => {
          hover("Turismo");
          Update("Turismo");
          UpdateState();
        }}
        id="Turismo"
      >
        Políticas Turismo
      </span>
      <h5
        onClick={() => {
          hover("Configuración");
          Update("Configuración");
          UpdateState();
        }}
        id="Configuración"
      >
        Configuración
      </h5>
    </div>
  );
}
