import React from "react";
import { Link } from "react-router-dom";
import "./Resumen.css";

export default function Resumen() {
  return (
    <section className="Resumen">
      <div className="Status-bar">
        <progress value="50" max="100" />
        <small>Completa tu Cuenta</small>
      </div>
      <p>Rendimiento General</p>

      <div className="tarjeta">
        <small className="title">Métricas de ventas</small>

        {/* Metricas de las Ventas */}
        <div className="flex">
          <span>
            <small className="small">Ventas Semanales</small>
            <h3>230</h3>
          </span>

          <span>
            <small className="small">Ventas Mensuales</small>
            <h3>1230</h3>
          </span>

          <span>
            <small className="small">Aporte a proyectos</small>
            <h3>$30.00</h3>
          </span>

          <span>
            <small className="small">Ventas canceladas</small>
            <h3>5</h3>
          </span>

          <span>
            <small className="small">Usuarios alcanzados</small>
            <h3>234</h3>
          </span>

          <span>
            <small className="small">Total Vendido</small>
            <h3>4'765.000</h3>
          </span>

          <span>
            <small className="small">Productos agotados</small>
            <h3>6</h3>
          </span>
        </div>
      </div>
      <br />

      <p>Tus Pendientes</p>
      <br />
      <div className="grind">
        <div className="cardd Ventas">
          <h5>Ventas para Preparar</h5>
          <h5>Experiencias para Preparar</h5>
          <small>No tienes ventas por preparar</small>
          <br />
          <hr />
          <Link>Ver ventas</Link>
        </div>

        <div className="cardd Reputacion">
          <h5>Reputación</h5>
          <br />
          <Link>Productos mas Vendidos</Link>
        </div>

        <div className="cardd Productos">
          <h5>Productos y Servicios</h5>
          <br />
          <Link>(5) Marketplace</Link>
          <Link>(1) Nómadas digitales</Link>
        </div>

        <div className="cardd Cupones">
          <h5>Cupones</h5>
          <Link>Nombre del cupón</Link>
          <br />
          <Link>Ver todos los cupones</Link>
        </div>
      </div>
    </section>
  );
}
