import React from "react";
import Envios from "./Envios";
import "./Pagos.css";

export default function Pago({ Uptadate }) {
  return (
    <section className="Pagos">
      <h1>¿Cómo recibirás tus pagos?</h1>
      <p>Escoge la opción que te conviene</p>

      {/* Datos del pago */}
      <form>
        <div>
          <div className="input-group mb-2">
            <input type="text" className="form-control" placeholder="Titular" />
          </div>
          <div className="input-group mb-2">
            <input
              type="text"
              className="form-control"
              placeholder="Cédula de ciudadanía"
            />
          </div>
          <div className="input-group mb-2">
            <input type="text" className="form-control" placeholder="NIT" />
          </div>
          <div className="input-group mb-2">
            <select className="form-control" placeholder="">
              <option>Entidad bancaria</option>
            </select>
          </div>
          <div className="input-group mb-2">
            <input
              type="text"
              className="form-control"
              placeholder="Numero de Cuenta"
            />
          </div>
          <div
            style={{
              width: "45%",
              display: "flex",
              justifyContent: "space-between",
              fontWeight: "bold",
            }}
          >
            <span className="checbox_txt">
              <input type="checkbox" name="" id="" /> Ahorro
            </span>
            <span className="checbox_txt">
              <input type="checkbox" name="" id="" /> Corriente
            </span>
          </div>
        </div>
      </form>
      <br />
      <button
        onClick={() => {
          Uptadate(<Envios Uptadate={Uptadate} />);
        }}
        className="btn btn-Registrarse"
      >
        Guardar y continuar
      </button>
    </section>
  );
}
