import React from "react";
import { Link } from "react-router-dom";
import "./Empezar.css";
import item1 from "./Frame.png";
import item2 from "./Frame-1.png";
import item3 from "./Frame-2.png";
import Nombre from "./Nombre";

export default function Empezar({ Uptadate }) {
  return (
    <section className="Empezar">
      <h1>Preferencias de la tienda</h1>
      <p>Cuéntanos sobre tu nido</p>

      <div className="items">
        <button className="card">
          <img src={item1} alt="" />
        </button>
        <button className="card">
          <img src={item2} alt="" />
        </button>
        <button className="card">
          <img src={item3} alt="" />
        </button>
      </div>
      <form action="">
        <div class="form-row">
          <div class="form-group col-md-6">
            <input
              type="text"
              class="form-control"
              id="inputDepartamento"
              placeholder="Departamento"
            />
          </div>
          <div class="form-group col-md-6">
            <input
              type="text"
              class="form-control"
              id="inputCiudad"
              placeholder="Ciudad"
            />
          </div>
          <div class="form-group col-md-6">
            <input
              type="text"
              class="form-control"
              id="inputAddres"
              placeholder="Dirección de tienda o residencia"
            />
          </div>
          <div class="form-group col-md-6">
            <input
              type="text"
              class="form-control"
              id="inputGoogle-maps"
              placeholder="Dirección google maps"
            />
          </div>
          <div class="form-group col-md-6">
            <input
              type="text"
              class="form-control"
              id="inputRNT"
              placeholder="RNT obligatorio para turismo"
            />
          </div>
          <div class="form-group col-md-6">
            <input
              type="text"
              class="form-control"
              id="inputCategoría"
              placeholder="Categoría"
            />
          </div>
          <div style={{ margin: "auto" }}>
            <Link
              onClick={() => {
                Uptadate(<Nombre Uptadate={Uptadate} />);
              }}
              className="btn btn-block btn-Registrarse"
            >
              Guardar y continuar
            </Link>
          </div>
        </div>
      </form>
    </section>
  );
}
