import React from "react";
import "./Header.css";
import logo from "../../../assets/logo.png";
import vacio from "../../../assets/vacio.png";
import Uno from "../../../assets/Uno.png";
import dos from "../../../assets/dos.png";
import tres from "../../../assets/tres.png";
import trocua from "../../../assets/trocua.png";
import cinco from "../../../assets/cinco.png";
import listo from "../../../assets/Listo.png";

export default function Header() {
  return (
    <section className="Header">
      <img src={logo} alt=" " />

      {/* Linea de Progreso */}
      <div className="Progreso">
        <span>
          <img src={Uno} alt=" " />
          <p>Empezando la tienda</p>
        </span>
        <span>
          <img src={vacio} alt=" " />
          <p>Ponle nombre a tu tienda</p>
        </span>
        <span>
          <img src={vacio} alt=" " />
          <p>Tu primer inventario</p>
        </span>
        <span>
          <img src={vacio} alt=" " />
          <p>Pagos</p>
        </span>
        <span>
          <img src={vacio} alt=" " />
          <p>Despachos</p>
        </span>
      </div>
    </section>
  );
}
