import React from "react";
import "./Inventario.css";
import Pagos from "./Pagos";
import marco from "../../../../assets/marco.png";

export default function Inventario({ Uptadate }) {
  return (
    <section className="Inventario">
      <h1>Creación de inventario</h1>
      <p>Aprende a colocar tus productos</p>

      {/* Inventario */}
      <div className="inventario-grid">
        <div>
          <h5>Primero la foto</h5>
          <p>
            Puedes añadir hsta 10 fotos en diferentes angulos para promocionar
            tus productos
          </p>
          <label htmlFor="Foto1">
            <img src={marco} alt=" " />
          </label>
          <label htmlFor="Foto2">
            <img src={marco} alt=" " />
          </label>
          <input type="file" id="Foto1" />
          <input type="file" id="Foto2" />
        </div>
        <div>
          <h5>Información del producto</h5>
          <form action="">
            <div style={{ margin: "0px" }} class="form-group">
              <input
                type="text"
                class="form-control"
                placeholder="Título del producto"
              />
            </div>
            <small style={{ color: "black" }}>
              Palabras claves de como encontraría tu producto
            </small>
            <br />
            <textarea
              placeholder="Descripción"
              className="form-control"
              rows="3"
            ></textarea>
            <small style={{ color: "black" }}>
              Cualidades o atributos de este elemento, procesos o historias
            </small>
            <div>
              <input type="checkbox" name="" /> <strong>Si es artesanía</strong>
            </div>
          </form>
        </div>
        <div>
          <h5>Componentes</h5>
          <input
            className="form-control"
            type="text"
            placeholder="Materiales"
          />
          <br />
          <h5>Precio</h5>
          <input
            className="form-control"
            type="text"
            placeholder="Pesos colombianos"
          />
          <br />
          <h5>Variantes</h5>
          <input
            className="form-control"
            type="text"
            placeholder="Materiales"
          />
        </div>
      </div>
      <br />
      <small style={{ color: "black" }}>
        Este artículo no está activo y estará disponibles para los compradores
        una vez la tienda esté completa al 100%
      </small>
      <div>
        <button
          className="btn btn-Registrarse"
          onClick={() => {
            Uptadate(<Pagos Uptadate={Uptadate} />);
          }}
        >
          Guardar y Continuar
        </button>
      </div>
    </section>
  );
}
