import React from "react";
import "./Envios.css";
import Fin from "./Fin";

export default function Envios({ Uptadate }) {
  return (
    <section className="Envios">
      <h1>¿Cómo harás tus envíos?</h1>
      <p>
        Acercate a la empresa de envios y pregunta precio y tiempo estimado de
        entrega para ciudades principales y resto del pais, ten en cuenta el
        empaque y proteccion del producto para que llegue en buenas condiciones,
        guarda la guia de envio y subela a la plataforma para que el comprador
        pueda saber donde se encuentra tu pedido.
      </p>

      <form action="">
        <div className="form-group">
          <input
            type="text"
            className="form-control"
            placeholder="Origen de envío"
          />
        </div>

        <div className="form-group">
          <input
            type="text"
            className="form-control"
            placeholder="Tiempo estimado de entrega"
          />
        </div>
        <div className="form-group">
          <input
            type="text"
            className="form-control"
            placeholder="Región de envío"
          />
        </div>
        <div className="form-group">
          <input
            type="text"
            className="form-control"
            placeholder="Precio Envío"
          />
        </div>
        <div className="form-group">
          <span style={{ fontWeight: "bold", fontSize: "small" }}>
            <input type="checkbox" /> Activar envío gratuito
          </span>
        </div>
        <div className="form-group">
          <input
            type="text"
            className="form-control"
            placeholder="Precio Envío"
          />
        </div>
        <p>
          Más adelante te explicamos como configurar los envíos y rastreos de
          pedidos
        </p>
      </form>
      <center>
        <button
          onClick={() => {
            Uptadate(<Fin Uptadate={Uptadate} />);
          }}
          className="btn btn-Registrarse"
        >
          Guardar y continuar
        </button>
      </center>
    </section>
  );
}
