import React, { useState } from "react";
import "./Dashboard.css";
import Drawerr from "@material-ui/core/Drawer";

import menuMain from "../../../assets/menu.png";
import Drawer from "./Drawer";
import Header from "./Header";
import Resumen from "./Resumen/Resumen";

export default function Dashboard() {
  const [info, setInfo] = useState({
    infoTxt: "Resumen",
    page: <Resumen />,
  });

  const [state, setState] = useState(false);

  const Update = (event, page) => {
    setInfo({
      infoTxt: event,
      page: page,
    });
  };

  const UpdateState = () => {
    setState(false);
  };

  return (
    <section className="MainDashboard">
      <Header />
      <img
        onClick={() => {
          setState(true);
        }}
        className="Menu-Main"
        src={menuMain}
        alt=" "
      />
      <Drawerr
        anchor="left"
        open={state}
        onClose={() => {
          setState(false);
        }}
      >
        <Drawer UpdateState={UpdateState} Update={Update} />
      </Drawerr>
      <div>
        <small className="smallMenu">
          Ventas {">"} {info.infoTxt}
        </small>

        {info.page}
      </div>
    </section>
  );
}
