import React from "react";
import "./Register.css";
import DashProve from "../../../assets/dashprove.png";
import google from "../../../assets/google.png";
import facebook from "../../../assets/facebook.png";
import { Link } from "react-router-dom";

export default function Dashboard() {
  return (
    <section className="Proveedores">
      <img className="dashprove" src={DashProve} alt=" " />
      <div className="Prove-form">
        <h1>Registratete como Proveedor</h1>
        <p>Tu pones el nido y nosotros las alas!</p>

        <form action="" className="form-group">
          <input
            type="text"
            className="form-control"
            placeholder="Nombre y Apellido"
          />
          <input
            type="number"
            className="form-control"
            placeholder="Cédula de ciudadanía"
          />
          <input
            type="email"
            className="form-control"
            placeholder="Correo electrónico"
          />
          <span>
            <input
              type="password"
              className="form-control"
              placeholder="Contraseña"
            />
          </span>
          <span className="checkboxs">
            <div>
              <input type="checkbox" />
              <span> Aceptas nuestra política y manejo de datos</span>
            </div>
            <div>
              <input type="checkbox" />
              <span> Aceptas nuestros términos y condiciones</span>
            </div>
          </span>
          <span className="icon-fast">
            <img src={google} alt=" " />
            <img src={facebook} alt=" " />
          </span>
          <div>
            <Link to="create-tienda" className="btn btn-block btn-Registrarse">
              Registrarse
            </Link>
          </div>
          <br />
          <span className="redirect">
            <Link>Ya tienes cuenta? Inicia sesión!</Link>
          </span>

          <footer>All rights Reserved</footer>
        </form>
      </div>
    </section>
  );
}
