import React from "react";
import "./Nombre.css";
import alert from "../../../../assets/alert.png";
import Inventario from "./Inventario";

export default function Nombre({ Uptadate }) {
  return (
    <section className="Nombre">
      <h1>El nombre de tu tienda</h1>
      <p>Cuéntanos sobre tu nido</p>

      <form>
        <div>
          <div className="input-group mb-2">
            <input
              type="text"
              className="form-control alert"
              placeholder="Escribe el nombre de tu tienda"
            />
            <div className="input-group-prepend">
              <div className="input-group-text">
                <img className="AvisoAlert" src={alert} />
              </div>
            </div>
          </div>
        </div>
        <small id="nameHelp" className="form-text AvisoAlert">
          El nombre ya no está disponible
        </small>
        <div className="form-group">
          <input
            type="text"
            className="form-control"
            placeholder="URL Video "
          />
        </div>
        <div className="form-group">
          <textarea
            placeholder="Descripción"
            className="form-control"
            rows="3"
          ></textarea>
        </div>
      </form>
      <div>
        <button
          onClick={() => {
            Uptadate(<Inventario Uptadate={Uptadate} />);
          }}
          className="btn btn-block btn-Registrarse"
        >
          Guardar y continuar
        </button>
      </div>
    </section>
  );
}
